import styles from "../Create.module.scss";
import root from "../../../Auth.module.scss";

import { useFormContext } from "react-hook-form";

export const Name = () => {
  const {
    register,
    formState: { errors },
  } = useFormContext();

  return (
    <>
      <span className={styles.create_tag}>
        
        <span className={styles.create_tag_desc}>
          &nbsp;&nbsp;
        </span>
      </span>
      <div>
        <input
          className={`${root.auth_input} ${
            errors.name && root.auth_input_error
          }`}
          placeholder=""
          {...register("name", {
            required: {
              value: true,
              message: "",
            },
            pattern: {
              value: /^\S+/,
              message: "",
            },
            minLength: {
              value: 2,
              message: "2",
            },
            maxLength: {
              value: 24,
              message: "24",
            },
          })}
        />
        <span className={root.auth_error}>{errors.name?.message}</span>
      </div>
    </>
  );
};
